.pop-res-cont {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.pop-res-cont .pop-res {
  width: clamp(80px, 30vw, 170px);
  height: clamp(80px, 30vw, 170px);
  border: 2px solid #303030;
  background: linear-gradient(54deg, #201f19 30%, #262626 59%);
  margin-bottom: .5em;
  border-radius: 1em;
  cursor: pointer;
  position: relative;
  -webkit-transition: .3s;
  transition: .3s;
}

.pop-res-cont .pop-res:hover {
  background: linear-gradient(54deg, #0e0d0b 30%, #202020 59%);
}

.pop-res-cont .pop-res:hover h3 {
  opacity: 1;
  bottom: 5px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.pop-res-cont .pop-res:hover figure div {
  top: 5px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}

.pop-res-cont .pop-res figure {
  height: 100%;
  width: 100%;
  position: relative;
}

.pop-res-cont .pop-res figure div {
  width: 50%;
  height: 50%;
  overflow: hidden;
  border-radius: 50%;
  border: 1px solid grey;
  position: relative;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-transition: .3s;
  transition: .3s;
}

.pop-res-cont .pop-res figure div img {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 100%;
}

.pop-res-cont .pop-res h3 {
  position: absolute;
  left: 50%;
  bottom: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
  font-family: 'Hubballi', cursive;
  opacity: 0;
  -webkit-transition: .3s;
  transition: .3s;
  text-transform: uppercase;
  font-size: clamp(0.8rem, 4vw, 1rem);
}

@media (max-width: 480px) {
  .pop-res-cont .pop-res h3 {
    opacity: 1;
    bottom: 5px;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
  }
  .pop-res-cont .pop-res figure div {
    top: 5px;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
  }
}
