.deliver-section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100vh;
  background: rgba(30, 30, 30, 0.9);
}

.deliver-section figure {
  width: 40%;
}

.deliver-section figure img {
  width: 100%;
}

.deliver-section article {
  width: 40%;
}

.deliver-section article section {
  font-family: 'Hubballi', cursive;
  border-top: 1px solid white;
  padding-top: 1.6em;
}

.deliver-section article section div {
  position: relative;
  margin-top: 1em;
}

.deliver-section article section label {
  font-size: clamp(1rem, 4vw, 1.5rem);
  position: absolute;
  bottom: 100%;
  right: 0;
}

.deliver-section article section input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  margin: 1em 0;
  width: 100%;
  height: 20px;
  border-radius: .4em;
  background: #555555;
  -webkit-transition: .3s;
  transition: .3s;
  cursor: pointer;
}

.deliver-section article section input:hover {
  -webkit-box-shadow: 0 0 2px white, 0 0 3px white;
          box-shadow: 0 0 2px white, 0 0 3px white;
}

.deliver-section article section input::-webkit-slider-thumb {
  -webkit-appearance: none;
          appearance: none;
  background: #a17109;
  width: 20px;
  height: 30px;
  border-radius: .2em;
}

.deliver-section article h1 {
  font-family: 'Hubballi', cursive;
  font-size: clamp(1rem, 3vw, 2rem);
  color: #b7eeee;
  text-shadow: 0 0 2px azure;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.deliver-section article h1 span {
  border-bottom: 2px solid orangered;
  color: orange;
  text-align: center;
  text-shadow: none;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.deliver-section article p {
  margin: 2em 0;
  font-size: clamp(0.8rem, 4vw, 1rem);
}

@media (max-width: 768px) {
  .deliver-section {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    height: auto;
  }
  .deliver-section article {
    width: 90%;
  }
  .deliver-section article section {
    margin: 3em 0;
  }
  .deliver-section figure {
    width: 70%;
    margin: 2em 0;
  }
}
