.comment-section {
  background: rgba(30, 30, 30, 0.65);
  padding: 4em 0 5em 0;
}

.comment-section h1 {
  font-family: 'Hubballi';
  text-align: center;
  font-size: 1.7rem;
  margin-bottom: .1em;
}

.comment-section h2,
.comment-section h3 {
  border-bottom: 1px solid antiquewhite;
  color: antiquewhite;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 80%;
  text-align: center;
  margin: 0 auto;
  font-family: 'Hubballi';
  font-size: 1rem;
}

.comment-section h3 {
  margin: 1em auto;
  color: aquamarine;
  border: 0;
  -webkit-transition: .3s;
  transition: .3s;
  cursor: pointer;
}

.comment-section h3:hover {
  color: aqua;
}

.comment-section section.outer-section {
  background: #303030;
  cursor: -webkit-grabbing;
  cursor: grabbing;
}

.comment-section section.outer-section section.inner-section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 1em 0;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}
