.cards-cont {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 97vw;
  max-width: 100%;
  margin: 3em auto;
  position: relative;
}

.cards-cont .card-outer {
  width: 300px;
  max-width: 95%;
  margin: 0 .25em .50em .25em;
  height: 400px;
  max-height: 135vw;
  -webkit-transition: .5s;
  transition: .5s;
}

.cards-cont .card-outer.scale {
  -webkit-transform: scale(0);
          transform: scale(0);
}

.cards-cont .card-inner {
  border: 1px solid white;
  width: 300px;
  height: 400px;
  max-height: 135vw;
  position: relative;
  -webkit-transition: .5s;
  transition: .5s;
}

.cards-cont .card-inner span.arrow-icon {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  left: 0;
  cursor: pointer;
  -webkit-transition: .5s;
  transition: .5s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.cards-cont .card-inner span.arrow-icon:hover svg {
  color: #5353ff;
  -webkit-filter: blur(1px);
          filter: blur(1px);
}

.cards-cont .card-inner span.arrow-icon svg {
  width: 35px;
  height: 35px;
  color: white;
  -webkit-filter: drop-shadow(0 0 3px black);
          filter: drop-shadow(0 0 3px black);
  -webkit-transition: .3s;
  transition: .3s;
  pointer-events: none;
}

.cards-cont .card-inner h2 {
  font-family: 'Hubballi', cursive;
  text-align: center;
  border-bottom: 2px solid white;
  display: block;
  padding: .3em 0;
  background: rgba(65, 105, 225, 0.6);
  font-size: clamp(0.9rem, 4vw, 1.5rem);
}

.cards-cont .card-inner button {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: 10%;
  padding: clamp(0.2em, 1.5vw, 0.7em) 0;
  width: 35%;
  min-width: 70px;
  border: 3px solid royalblue;
  border-radius: .3em;
  outline: 0;
  background: #122f41;
  font-family: 'Hubballi', cursive;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  -webkit-box-shadow: 0 0 2px black, 0 0 4px black;
          box-shadow: 0 0 2px black, 0 0 4px black;
  -webkit-transition: .3s;
  transition: .3s;
}

.cards-cont .card-inner button:hover {
  background: #1e5170;
  border-color: #000b2c;
  -webkit-box-shadow: 0;
          box-shadow: 0;
}

@media (max-width: 768px) {
  .cards-cont .card-outer,
  .cards-cont .card-inner {
    width: 45vw;
    height: 60vw;
  }
  .cards-cont .card-inner span.arrow-icon {
    display: none;
  }
}
