article.steps-cont {
  min-height: 100vh;
  background: rgba(30, 30, 30, 0.9);
}

article.steps-cont h1 {
  text-align: center;
  padding: 1.5em 0;
  font-size: 1.2rem;
  font-family: 'Hubballi', cursive;
}

article.steps-cont h1 span {
  color: orangered;
  border-bottom: 1px solid orangered;
  font-family: 'Manrope', sans-serif;
  font-size: 1.1rem;
}

article.steps-cont section.wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

article.steps-cont section.wrap article {
  width: 35%;
  font-family: 'Hubballi', cursive;
}

article.steps-cont section.wrap article p {
  line-height: 1em;
}

article.steps-cont section.wrap article h2 {
  border-bottom: 1px solid aqua;
  margin-bottom: .5em;
  text-transform: uppercase;
  text-shadow: 0 0 2px aqua, 0 0 4px aqua;
}

article.steps-cont figure {
  width: 50%;
  position: relative;
  border: 4px solid rgba(255, 174, 0, 0.719);
  border-radius: .3em;
}

article.steps-cont figure span {
  position: absolute;
  top: 50%;
  cursor: pointer;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: .3s;
  transition: .3s;
}

article.steps-cont figure span:first-of-type {
  right: 98%;
}

article.steps-cont figure span:first-of-type:hover {
  right: 100%;
}

article.steps-cont figure span:last-of-type {
  left: 98%;
}

article.steps-cont figure span:last-of-type:hover {
  left: 100%;
}

article.steps-cont figure span:hover svg {
  -webkit-filter: drop-shadow(0 0 5px orange);
          filter: drop-shadow(0 0 5px orange);
}

article.steps-cont figure span svg {
  width: clamp(25px, 4vw, 50px);
  height: clamp(25px, 4vw, 50px);
  pointer-events: none;
  -webkit-transition: .3s;
  transition: .3s;
}

article.steps-cont figure div.outer {
  overflow: hidden;
  z-index: 999;
  height: 28vw;
  position: relative;
}

article.steps-cont figure div.outer div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

article.steps-cont figure div.outer div img {
  width: 100%;
}

@media (max-width: 768px) {
  article.steps-cont section.wrap {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  article.steps-cont section.wrap figure {
    margin-bottom: 2em;
    width: 80%;
  }
  article.steps-cont section.wrap figure span svg {
    width: clamp(25px, 7vw, 50px);
    height: clamp(25px, 7vw, 50px);
  }
  article.steps-cont section.wrap figure div.outer {
    height: 45vw;
  }
  article.steps-cont section.wrap article {
    width: 90%;
  }
}

@media (max-width: 480px) {
  article.steps-cont {
    min-height: auto;
    padding-bottom: 3em;
  }
}
