.cards-section {
  background: rgba(30, 30, 30, 0.65);
  min-height: 100vh;
}

.cards-section h1 {
  text-align: center;
  padding-top: 1em;
  padding-bottom: .2em;
  font-family: 'Hubballi', cursive;
  border-bottom: 2px solid white;
  margin: 0 auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-size: clamp(1rem, 5vw, 1.5rem);
}

.cards-section div.see-more {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.cards-section div.see-more h2 {
  font-family: 'Hubballi', cursive;
  font-size: clamp(1rem, 5vw, 1.5rem);
  margin-bottom: .5em;
}

.cards-section div.see-more h2 span {
  color: coral;
  -webkit-transition: .3s;
  transition: .3s;
  cursor: pointer;
  position: relative;
}

.cards-section div.see-more h2 span::before, .cards-section div.see-more h2 span::after {
  -webkit-transition: .3s;
  transition: .3s;
  content: '';
  position: absolute;
  left: 50%;
  width: 100%;
  height: 2px;
  background: coral;
  -webkit-transform: translateX(-50%) scaleX(0);
          transform: translateX(-50%) scaleX(0);
}

.cards-section div.see-more h2 span::before {
  top: 0;
}

.cards-section div.see-more h2 span::after {
  bottom: -2px;
}

.cards-section div.see-more h2 span:hover {
  color: #facab8;
}

.cards-section div.see-more h2 span:hover::after {
  -webkit-transform: translateX(-50%) scaleX(1);
          transform: translateX(-50%) scaleX(1);
}

.cards-section div.see-more h2 span:hover::before {
  -webkit-transform: translateX(-50%) scaleX(1);
          transform: translateX(-50%) scaleX(1);
}

.cards-section div.see-more img {
  width: 200px;
}

@media (max-width: 480px) {
  .cards-section div.see-more h2 {
    text-align: center;
  }
  .cards-section div.see-more h2 span {
    display: block;
  }
}
