nav.main-nav {
  background: #303030;
  border-bottom: 1px solid #929292;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  color: white;
  top: 0;
  width: 100%;
  z-index: 999999;
}

nav.main-nav figure {
  height: 60px;
  width: 20%;
  min-width: -webkit-min-content;
  min-width: -moz-min-content;
  min-width: min-content;
}

nav.main-nav figure img {
  height: 100%;
  display: block;
  margin: 0 auto;
}

nav.main-nav ul {
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 60%;
}

nav.main-nav ul li {
  -webkit-transition: .2s;
  transition: .2s;
  margin: 0 clamp(0.2em, 2vw, 1em);
  margin: 0 1em;
  cursor: pointer;
  font-weight: 400;
  position: relative;
  font-size: clamp(0.8rem, 3vw, 1rem);
}

nav.main-nav ul li svg {
  position: absolute;
  top: 0;
  opacity: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  -webkit-transition: .2s;
  transition: .2s;
  color: goldenrod;
}

nav.main-nav ul li.ul-order {
  color: cornflowerblue;
  text-shadow: 0 0 2px royalblue, 0 0 4px royalblue;
}

nav.main-nav ul li:hover {
  color: goldenrod;
  text-shadow: 1px 1px 2px gold, 0 0 3px gold;
}

nav.main-nav ul li:hover svg {
  opacity: 1;
  top: 100%;
}

nav.main-nav section {
  width: 20%;
  min-width: -webkit-min-content;
  min-width: -moz-min-content;
  min-width: min-content;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

nav.main-nav section div {
  width: 25px;
  height: 25px;
  position: relative;
  cursor: pointer;
  margin-right: .5em;
}

nav.main-nav section div span {
  position: absolute;
  width: 100%;
  height: 1.5px;
  left: 0;
  background: white;
  pointer-events: none;
  -webkit-transition: .5s;
  transition: .5s;
}

nav.main-nav section div span:nth-child(1) {
  top: 15%;
}

nav.main-nav section div span:nth-child(2) {
  top: 50%;
}

nav.main-nav section div span:nth-child(3) {
  top: 85%;
}

nav.main-nav section h5 {
  margin: 0 .5em;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s;
  padding: .5em 1em;
  white-space: nowrap;
}

nav.main-nav section h5.sign-h5 {
  border: 1px solid blue;
  border-radius: .2em;
  font-weight: 600;
  background-color: #404040;
  font-family: 'Hubballi', cursive;
}

nav.main-nav section h5.sign-h5:hover {
  -webkit-box-shadow: 0 0 2px blue, 0 0 4px blue, 0 0 8px blue, 0 0 16px blue;
          box-shadow: 0 0 2px blue, 0 0 4px blue, 0 0 8px blue, 0 0 16px blue;
  color: orange;
}

nav.main-nav section h5.join-h5 {
  border: 1px solid whitesmoke;
  border-color: transparent transparent transparent whitesmoke;
}

nav.main-nav section h5.join-h5:hover {
  background: #505050;
  border: 1px solid whitesmoke;
  border-color: whitesmoke;
  border-radius: .2em;
}

@media (max-width: 768px) {
  nav.main-nav {
    padding: 1em 0;
  }
  nav.main-nav figure {
    display: none;
  }
}

@media (max-width: 480px) {
  nav.main-nav ul li {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  nav.main-nav ul li p {
    display: none;
  }
  nav.main-nav ul li svg {
    width: 22px;
    height: 22px;
    opacity: 1;
    position: static;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  nav.main-nav ul li.ul-order svg {
    color: cornflowerblue;
    width: 24px;
    height: 24px;
    -webkit-filter: drop-shadow(0 0 2px blue);
            filter: drop-shadow(0 0 2px blue);
  }
  nav.main-nav section h5 {
    display: none;
  }
}
