.card-inner-menu {
  background: #252525;
  border: 1px solid white;
  border-width: 1px 1px 1px 0;
  z-index: 99999;
  display: none;
  position: absolute;
  left: 100.5%;
  top: 0;
  width: 0;
  height: 100%;
  -webkit-transition: .5s;
  transition: .5s;
  overflow: hidden;
}

.card-inner-menu table {
  width: 100%;
  border-collapse: collapse;
}

.card-inner-menu tr {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.card-inner-menu tr.imgs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #303030;
}

.card-inner-menu tr.imgs td {
  border-right: 2px solid black;
  width: 100%;
}

.card-inner-menu tr.imgs td img {
  width: 50px;
  display: block;
  margin: 0 auto;
}

.card-inner-menu tr:not(.imgs) td {
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  padding: .5em 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  border-right: 2px solid black;
}

.card-inner-menu tr:not(.imgs) td:hover {
  background: #353535;
}

.card-inner-menu tr:not(.imgs) td figure {
  width: 5vw;
  height: 5vw;
  min-width: 60px;
  min-height: 60px;
}

.card-inner-menu tr:not(.imgs) td figure img {
  height: 100%;
  width: 100%;
}

.card-inner-menu tr:not(.imgs) td div {
  width: 72%;
  position: relative;
}

.card-inner-menu tr:not(.imgs) td div h4 {
  text-align: center;
  font-family: 'Hubballi', cursive;
  font-size: 1rem;
}

.card-inner-menu tr:not(.imgs) td div h5 {
  color: greenyellow;
  font-weight: 700;
  background: #303030;
  width: 30%;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  font-size: .8rem;
}

@media (max-width: 1024px) {
  .card-inner-menu tr:not(.imgs) td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .card-inner-menu tr:not(.imgs) td figure {
    margin: 0 auto;
  }
  .card-inner-menu tr:not(.imgs) td div {
    width: 100%;
  }
  .card-inner-menu tr:not(.imgs) td div h5 {
    position: static;
    width: 100%;
    margin-top: .5em;
  }
}

@media (max-width: 768px) {
  .card-inner-menu {
    display: none !important;
  }
}
