* {
  margin: 0;
  padding: 0;
  font-weight: 300;
}

body {
  font-family: 'Manrope', sans-serif;
  min-height: 100vh;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: url("https://images.unsplash.com/photo-1546484396-fb3fc6f95f98?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80");
  background-size: cover;
  background-attachment: fixed;
  color: white;
  width: 100vw;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  width: 4px;
  background: #202020;
}

body::-webkit-scrollbar-thumb {
  width: 100%;
  background: #606060;
}

div.wrap-html {
  overflow-x: hidden;
}

section.uparrow {
  position: fixed;
  right: 0;
  bottom: 0;
  z-index: 9999999999;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  opacity: 0;
  pointer-events: none;
}

section.uparrow:hover svg {
  color: orange;
}

section.uparrow svg {
  width: 30px;
  height: 30px;
  color: white;
}
