.entry-header {
  background: rgba(30, 30, 30, 0.9);
  height: calc(100vh - 61px);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
}

.entry-header section,
.entry-header figure {
  -webkit-transition: 2s;
  transition: 2s;
}

.entry-header span.entry-icon {
  position: absolute;
  left: 50%;
  top: 0;
  -webkit-transform: translateX(-35%);
          transform: translateX(-35%);
  color: aliceblue;
  -webkit-filter: drop-shadow(0 0 2px white);
          filter: drop-shadow(0 0 2px white);
}

.entry-header span.entry-icon svg {
  width: 70px;
  height: 70px;
}

.entry-header section {
  width: 40%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-transform: translateX(-150%);
          transform: translateX(-150%);
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.entry-header section h1 {
  font-size: clamp(2rem, 5vw, 3.5rem);
  font-family: 'Hubballi', cursive;
  font-style: italic;
}

.entry-header section h1 span.mark {
  color: orange;
}

.entry-header section h1 span.mark span {
  -webkit-animation: dot_anim 5s;
          animation: dot_anim 5s;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes dot_anim {
  0% {
    opacity: 1;
  }
  18% {
    opacity: 0;
  }
  36% {
    opacity: 1;
  }
  54% {
    opacity: 0;
  }
  62% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes dot_anim {
  0% {
    opacity: 1;
  }
  18% {
    opacity: 0;
  }
  36% {
    opacity: 1;
  }
  54% {
    opacity: 0;
  }
  62% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.entry-header section p.header-text {
  margin: .2em 0 .5em 0;
  font-size: clamp(0.8rem, 1.5vw, 1rem);
}

.entry-header section button {
  width: 120px;
  outline: 0;
  cursor: pointer;
  background: #411f0e;
  border: 3px solid orangered;
  border-radius: .4em;
  -webkit-transition: .3s;
  transition: .3s;
  position: relative;
}

.entry-header section button p {
  padding: .8em 0;
  font-size: 1.1rem;
  font-family: 'Hubballi', cursive;
  color: white;
  text-transform: uppercase;
  letter-spacing: -1.5px;
  z-index: 999;
  background: #411f0e;
  display: block;
  position: relative;
  -webkit-transition: .3s;
  transition: .3s;
  font-weight: bold;
}

.entry-header section button span {
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  -webkit-transition: .3s;
  transition: .3s;
}

.entry-header section button span svg {
  width: 25px;
  color: orange;
  background: #411f0e;
  border: 3px solid royalblue;
  border-width: 3px 3px 3px 0;
  height: 25px;
}

.entry-header section button:hover {
  border-color: royalblue;
  -webkit-box-shadow: 0 0 4px green, 0 0 8px green;
          box-shadow: 0 0 4px green, 0 0 8px green;
}

.entry-header section button:hover p {
  color: cornflowerblue;
}

.entry-header section button:hover span {
  left: 102.5%;
}

.entry-header section button:hover svg {
  -webkit-box-shadow: 0 0 4px green, 0 0 8px green;
          box-shadow: 0 0 4px green, 0 0 8px green;
}

.entry-header figure {
  width: 40%;
  -webkit-transform: translateX(150%);
          transform: translateX(150%);
}

.entry-header figure img {
  width: 100%;
}

@media (max-width: 480px) {
  .entry-header {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .entry-header span.entry-icon {
    display: none;
  }
  .entry-header button {
    margin: 0 auto;
  }
  .entry-header section {
    width: 90%;
    margin: 0 auto;
  }
  .entry-header section p.header-text {
    margin: 1em 0;
  }
  .entry-header figure {
    width: 100%;
  }
}
