.nav-menu {
  background: #202020;
  position: absolute;
  right: 0;
  top: 100%;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  -webkit-transition: .5s;
  transition: .5s;
  border: 1px solid royalblue;
  width: 280px;
  max-width: 99%;
  color: white;
  z-index: 999999;
}

.nav-menu ol {
  list-style: none;
}

.nav-menu ol li:not(.line) {
  font-family: 'Hubballi', cursive;
  padding: 1em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transition: .3s;
  transition: .3s;
  cursor: pointer;
  font-size: 1.05rem;
}

.nav-menu ol li:not(.line).credits {
  display: none;
}

.nav-menu ol li:not(.line) img {
  width: 50%;
  margin: 0 auto;
}

.nav-menu ol li:not(.line) svg {
  margin-right: .5em;
  color: white !important;
  width: 20px;
  height: 20px;
}

.nav-menu ol li:not(.line).order {
  color: cornflowerblue;
  text-shadow: 0 0 2px blue;
  text-transform: uppercase;
  text-align: center;
}

.nav-menu ol li:not(.line):hover {
  background: #303030;
  color: orange;
}

.nav-menu ol li.line {
  margin: 1em auto;
  border: 1px solid white;
  background: white;
  border-radius: 50%;
  width: 80%;
}

@media (max-width: 768px) {
  .nav-menu ol li:not(.line).credits {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
}
