.ship-section {
  height: 100vh;
  background: rgba(30, 30, 30, 0.65);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.ship-section figure {
  width: 40%;
  height: 37vw;
  position: relative;
  background: green;
}

.ship-section figure span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: red;
  position: absolute;
}

.ship-section figure img {
  width: 100%;
}

.ship-section section {
  width: 40%;
}

.ship-section section h1 {
  font-family: 'Hubballi', cursive;
  font-size: clamp(1.1rem, 2vw, 2rem);
  color: #b7eeee;
  text-shadow: 0 0 2px azure;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.ship-section section h1 span {
  border-bottom: 2px solid orangered;
  color: orange;
  text-shadow: none;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.ship-section section p {
  margin: 2em 0;
  font-size: clamp(0.8rem, 3vw, 1rem);
}

.ship-section section div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.ship-section section div h2,
.ship-section section div h3 {
  font-family: 'Hubballi', cursive;
  font-size: clamp(1rem, 4vw, 1.2rem);
  color: #f0ad5d;
}

.ship-section section div h2 span,
.ship-section section div h3 span {
  display: block;
  text-align: center;
  color: #f08963;
  font-weight: 700;
}

@media (max-width: 768px) {
  .ship-section {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .ship-section section {
    width: 90%;
  }
  .ship-section section h1 {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .ship-section section h1 span {
    margin-left: .5em;
  }
  .ship-section section h1 svg {
    margin: 0 .5em;
  }
  .ship-section figure {
    height: 60vw;
    width: auto;
    min-height: 240px;
  }
  .ship-section figure img {
    height: 100%;
    width: auto;
  }
}

@media (max-width: 480px) {
  .ship-section {
    height: auto;
  }
  .ship-section figure {
    margin: 3em 0;
  }
  .ship-section section {
    margin-bottom: 3em;
  }
}
