.comment {
  width: clamp(240px, 30vw, 350px);
  height: clamp(300px, 31vw, 400px);
  background: #202020;
  border-radius: 1.5em .1em;
  pointer-events: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0 2em;
  border: 1px solid #505050;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-transition: .5s;
  transition: .5s;
}

.comment div.author {
  height: clamp(70px, 10vw, 140px);
  background: #252525;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  border-radius: 1.5em .1em 0 0;
}

.comment div.author figcaption {
  font-family: 'Hubballi', cursive;
  text-transform: uppercase;
  font-size: clamp(0.9rem, 3vw, 1.1rem);
  text-shadow: 0 0 1px white, 0 0 2px white;
}

.comment div.author figure {
  width: clamp(50px, 7.5vw, 93px);
  height: clamp(50px, 7.5vw, 93px);
  position: relative;
  border-radius: 50%;
  overflow: hidden;
}

.comment div.author figure img {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 100%;
}

.comment div.text {
  position: relative;
  background: #272727;
  width: 85%;
  height: 55%;
  margin: auto;
  border-radius: 0 .3em 0 .3em;
}

.comment div.text p {
  padding: 1em;
  text-align: center;
  font-family: 'Hubballi', cursive;
  font-size: clamp(0.9rem, 3vw, 1rem);
}

.comment div.text svg {
  position: absolute;
  width: clamp(15px, 3vw, 25px);
  height: clamp(15px, 3vw, 25px);
}

.comment div.text svg:first-child {
  left: 0;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.comment div.text svg:last-child {
  right: 0;
  bottom: 0;
  -webkit-transform: translate(50%, 50%);
          transform: translate(50%, 50%);
}

.comment div.text div {
  position: absolute;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.comment div.text div span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.comment div.text div span svg {
  position: static;
  -webkit-transform: none !important;
          transform: none !important;
  width: clamp(15px, 2vw, 20px);
  height: clamp(15px, 2vw, 20px);
}
