footer {
  background: #303030;
  padding: 5em;
}

footer section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

footer section:first-of-type {
  margin-bottom: 2em;
}

footer section figure {
  width: 20%;
  min-width: 100px;
}

footer section figure img {
  width: 100%;
  margin: 0 auto;
  display: block;
}

footer section nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 50%;
  min-width: -webkit-max-content;
  min-width: -moz-max-content;
  min-width: max-content;
  border-left: 2px solid coral;
  border-right: 2px solid coral;
  padding: 0 1em;
}

footer section nav ul {
  list-style: none;
  margin: 0 .5em;
}

footer section nav ul li {
  font-family: 'Hubballi', cursive;
}

footer section nav ul li:first-child {
  font-size: clamp(1rem, 5vw, 2rem);
  color: coral;
  margin-bottom: .5em;
}

footer section nav ul li:not(li:first-child) {
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  font-size: clamp(0.9rem, 6vw, 1rem);
  margin-bottom: .2em;
}

footer section nav ul li:not(li:first-child):hover {
  color: royalblue;
}

footer section p {
  text-align: center;
  font-family: 'Hubballi', cursive;
  margin: .5em 0;
}

footer section span, footer section a {
  cursor: pointer;
  color: white;
  text-decoration: none;
}

footer section span:hover svg, footer section a:hover svg {
  color: cornflowerblue;
}

footer section span svg, footer section a svg {
  width: 30px;
  height: 30px;
  -webkit-transition: .3s;
  transition: .3s;
}

@media (max-width: 768px) {
  footer {
    padding: 1em 0;
  }
  footer section {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  footer section nav {
    margin: 1em 0;
  }
}
