.sponsor-cont {
  background: #303030f5;
  padding: 1em 0 2em 0;
}

.sponsor-cont h1 {
  text-align: center;
  font-size: 1rem;
  font-family: 'Hubballi', cursive;
  padding-bottom: 1em;
}

.sponsor-cont div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.sponsor-cont div img {
  width: 10%;
  min-width: 40px;
  cursor: pointer;
  opacity: .4;
  -webkit-transition: .3s;
  transition: .3s;
}

.sponsor-cont div img:hover {
  opacity: 1;
}
