.step-counter {
  margin: 3em 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.step-counter .fill {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  position: relative;
}

.step-counter .fill mark {
  position: absolute;
  height: 100%;
  width: 0;
  left: 0;
  top: 0;
  background: #707070;
  mix-blend-mode: color;
  -webkit-transition: .5s;
  transition: .5s;
}

.step-counter div {
  width: clamp(30px, 6vw, 40px);
  height: clamp(30px, 6vw, 40px);
  background: linear-gradient(54deg, #d7c600 30%, #d5a700 59%);
  border: 2px solid orange;
  color: white;
  font-size: clamp(0.85rem, 4vw, 1rem);
  text-shadow: 0 0 1px black, 0 0 2px black, 0 0 3px black;
  font-weight: 700;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.step-counter span {
  height: 5px;
  width: 30px;
  background: yellow;
}
